import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, NavigationEnd, Router } from "@angular/router";
import { OfflineService } from "@app/services/offline.service";
const ROUTE_DATA = [
  // "/app/purchase/",
  "/app/purchase/welcome/",
  "/app/purchase/property-info/",
  "/app/purchase/military-info/",
  "/app/purchase/personal-info/",
  "/app/purchase/income-info/",
  "/app/purchase/assets-info/",
  "/app/purchase/gov/",
  "/app/purchase/credit-score/",
];
const TABS_DATA = [];
@Component({
  selector: "app-index",
  templateUrl: "./index.component.html",
  styleUrls: ["./index.component.css"],
})
export class IndexComponent implements OnInit {
  a: number = 0;
  lastStepGreen: Boolean = false;
  isMilitaryStep: boolean = false;
  constructor(private router: Router, private offlineService: OfflineService) {
    this.router.events.subscribe((x) => {
      if (window.location.pathname == "/app/purchase/credit-score/3") {
        this.lastStepGreen = true;
        console.log(this.lastStepGreen);
      } else {
        this.lastStepGreen = false;
      }
      if (x instanceof NavigationEnd) {
        this.checkMilitaryModuleExist();
        console.log(x);
        var d = x.urlAfterRedirects.replace(/[0-9]/g, "");
        ROUTE_DATA.filter((obj, i) => {
          if (obj == d) {
            this.a = i;
          }
        });
        // if (x.urlAfterRedirects === "/app/purchase") {
        //   this.a = 0;
        // } else {
        //   ROUTE_DATA.filter((obj, i) => {
        //     if (new RegExp(obj + "(.*?)").test(x.urlAfterRedirects)) {
        //       this.a = i + 1;
        //     }
        //   });
        // }
      }
      //this.stepNav();
    });
  }
  checkMilitaryModuleExist() {
    this.isMilitaryStep = false;
    let offline: any = localStorage.getItem("offline");
    if (offline) {
      offline = JSON.parse(offline);
      if (offline.isMillitary) {
        this.isMilitaryStep = true;
      }
    }
  }

  ngOnInit() {
    this.checkMilitaryModuleExist();
  }

  stepNav() {
    const stepData = this.offlineService.getStep();
    console.log(stepData);
    this.router.navigate(["/app/" + ROUTE_DATA[stepData.step - 1]]);
  }
}
